.App {
  text-align: center;
}

.divTable{
  display: table;
  max-width: 20em;
  float: right;
  display: flex;
  align-items: center;
}

.divTableRow{
  display: table-row;
}

.divTableHeading{
  background-color: #eee;
  display: table-header-group;
  font-weight: bold;
}

.divTableCell,
.divTableHead{
  border: 1px solid #999999;
  display: table-cell;
  padding: 3px 10px;
}

.divTableFot{
  background-color: #eee;
  display: table-footer-group;
  font-weight: bold;
}

.divTableBody{
  display: table-row-group;
}

.divTableCell:nth-child(1n){
  background-color: gainsboro;
}

.container{
  display: flex;
  opacity: 0.9;
  background-color: #99aaab;
  justify-content: center;
}

img{
  /* display: none; */
  height: 16em;
  border: 3px solid red;
}

input {
  box-sizing: border-box;
  font-size: 2rem;
  padding: 1rem;
  display: block;
  margin: 2rem auto;
}

body{
  background-image: url("https://upload.wikimedia.org/wikipedia/commons/thumb/9/98/International_Pok%C3%A9mon_logo.svg/1200px-International_Pok%C3%A9mon_logo.svg.png");
  background-size: cover;
}